/**
 * dwSelectCenter directive - wrapper for ffSelect, but automatically and transparently populates dropdown.
 * If initial evaluated ffModel is undefined, inits ffModel with state.getCurrentCenterId()
 * If ffOptionAll is true, centers unshifted with {id: 'all', name: 'All'} center and it set as current center.
 */
export default angular.module('deskworks.dwSelectCenter', [
  'deskworks.state',
  'flexForms',
  'deskworks.helper',
  'deskworks.center.service',
  'deskworks.Admin',
])
  .directive('dwSelectCenter', function($q, $rootScope, $timeout, $compile, dwAlerts, ffHelper, state, helper, centerService, Admin) {
    return {
      restrict: 'E',
      scope: true,
      controller: function() {},
      controllerAs: 'dwSelectCenter',

      compile: function(tElement, tAttrs) {
        const input = angular.element('<ff-select name="center" flex="auto"></ff-select>');
        const options = angular.element('<option ng-repeat="center in dwSelectCenter.centers" ng-value="center.id" label="{{center.name}}">{{center.name}}</option>');

        options.attr('ng-style', 'center.color ? { color: center.color } : {}');

        input.attr('ff-style', 'dwSelectCenter.curCenter.color ? {color: dwSelectCenter.curCenter.color} : {}');
        input.attr('ff-model', tAttrs.ffModel || 'dwSelectCenter.curCenterId');
        input.attr('ff-change', 'dwSelectCenter.onCenterChange()');

        input.append(options);

        tElement.append(input);

        /**
         * Link function
         */
        return function link($scope, $element, $attrs, dwSelectCenter) {
          if (!tAttrs.ffModel) { // If no ffModel, use internal ffModel
            $scope.$watch(() => state.getCurrentCenterId(), centerId => {
              dwSelectCenter.curCenterId = centerId;
              dwSelectCenter.curCenter = helper.find(dwSelectCenter.centers, dwSelectCenter.curCenterId);
            });
          }

          /**
           * Current center change handler
           */
          dwSelectCenter.onCenterChange = function() {
            dwSelectCenter.curCenter = helper.find(dwSelectCenter.centers, dwSelectCenter.curCenterId);
            if (!tAttrs.ffModel) {
              dwSelectCenter.curCenter = helper.find(dwSelectCenter.centers, dwSelectCenter.curCenterId);
              state.setCurrentCenterId(dwSelectCenter.curCenterId);
            } else {
              dwSelectCenter.curCenter = helper.find(dwSelectCenter.centers, $scope.$eval(tAttrs.ffModel));
            }
          };

          /**
           * Refresh centers
           */
          $rootScope.$on('CENTERS:REFRESH', function() {
            refreshCenters();
          });

          refreshCenters();

          function refreshCenters() {
            const promises = {
              centers: centerService.getCenters()
                .catch(err => dwAlerts.error(err, 'dwSelectCenter: failed to query centers.'))
            };

            if (state.isLoggedAdmin()) promises.admin = Admin.get(state.getLoggedUserId())
              .catch(err => dwAlerts.error(err, 'dwSelectCenter: failed to get admin.'));

            return $q.all(promises)
              .then(result => {
                dwSelectCenter.centers = result.centers
                  .filter(center => { // filter centers for admins
                    if (result.admin) return !!(helper.find(result.admin.centers, center.id) || {}).permission;
                    return true;
                  });

                if ($attrs.ffOptionAll && $attrs.ffOptionAll !== 'false' && dwSelectCenter.centers.length > 1) {
                  dwSelectCenter.centers.unshift({ id: 0, name: 'All Centers' });
                }

                if (tAttrs.ffModel) {
                  const model = $scope.$eval(tAttrs.ffModel);
                  if (angular.isUndefined(model)) {
                    dwSelectCenter.curCenterId = dwSelectCenter.centers[0].id;
                    $scope.$eval(tAttrs.ffModel + ' = dwSelectCenter.curCenterId');
                  } else {
                    dwSelectCenter.curCenterId = model;
                  }
                } else {
                  dwSelectCenter.curCenterId = state.getCurrentCenterId() || dwSelectCenter.centers[0] && dwSelectCenter.centers[0].id;
                }

                dwSelectCenter.curCenter = helper.find(dwSelectCenter.centers, dwSelectCenter.curCenterId);
              });
          }
        };
      }
    };
  });
