export default angular.module('deskworks.ReservationField', [
  'ngResource',
  'deskworks.config',
])

  .factory('ReservationField', function($resource, $filter, $timeout, deskworksConfig, helper) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/reservation-fields/:id'), {}, {
      update: { method: 'PUT' }
    });

    return {
      query: () => res.query().$promise
        .then(fields => addFilters(fields))
        .then(fields => addDeserializers(fields))
        .then(fields => addSerializers(fields)),

      save: field => {
        if (field.id) return res.update({ id: field.id }, field).$promise;
        return res.save(field).$promise
          .then(result => { field.id = result.id; });
      },

      delete: id => res.delete({ id }).$promise,
    };

    function addFilters(fields) {
      const notEmpty = v => v || v === 0 || v === false;
      const types = {
        date: (f, v) => notEmpty(v) ? $filter('date')(v, 'mediumDate') : '-',
        customPrice: (f, v) => notEmpty(v) ? $filter('ncurrency')(v) : '-',
        extraPrice: (f, v) => notEmpty(v) ? $filter('ncurrency')(v) : '-',
        dropdown: (f, v) => notEmpty(v) ? f.options.find(o => o.id == v).name : '-',
        checkbox: (f, v) => notEmpty(v) ? $filter('yesno')(v) : '-',
      };
      fields.forEach(f => { f.$$filter = types[f.type] || ((f, v) => notEmpty(v) ? v : '-'); });
      return fields;
    }

    function addDeserializers(fields) {
      const types = {
        date: (f, v) => $filter('toDate')(v),
        customPrice: (f, v) => isNaN(+v) ? v : +v,
        extraPrice: (f, v) => isNaN(+v) ? v : +v,
        dropdown: (f, v) => isNaN(+v) ? v : +v,
        number: (f, v) => isNaN(+v) ? v : +v,
        checkbox: (f, v) => v === 'true' ? true : v === 'false' ? false : v,
      };
      fields.forEach(f => { f.$$deserialize = types[f.type] || ((f, v) => v); });
      return fields;
    }

    function addSerializers(fields) {
      const types = {
        date: (f, v) => $filter('date')(v, 'yyyy-MM-dd'),
        customPrice: (f, v) => isNaN(+v) ? v : +v,
        extraPrice: (f, v) => isNaN(+v) ? v : +v,
        dropdown: (f, v) => isNaN(+v) ? v : +v,
        number: (f, v) => isNaN(+v) ? v : +v,
        checkbox: (f, v) => v === 'true' ? true : v === 'false' ? false : v,
      };
      fields.forEach(f => { f.$$serialize = types[f.type] || ((f, v) => v); });
      return fields;
    }
  });
