/**
 * Options for Message Board notices period in User Profile -> Login Information
 */
export default angular.module('deskworks.optsMessageBoardNotices', [])

  .factory('optsMessageBoardNotices', function($rootScope, $translate) { 'use strict';
    const opts = [
      { id: 'immediately', _name: 'USER_PROFILE.MESSAGE_BOARD_NOTICES.IMMEDIATELY' },
      { id: 'daily', _name: 'USER_PROFILE.MESSAGE_BOARD_NOTICES.DAILY' },
      { id: 'weekly', _name: 'USER_PROFILE.MESSAGE_BOARD_NOTICES.WEEKLY' },
      { id: 'never', _name: 'USER_PROFILE.MESSAGE_BOARD_NOTICES.NEVER' },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(opt => { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
