import './scroll-pointer-events-none.scss';

/**
 * scrollPointerEventsNone directive - sets pointer-events: none; on scroll for scroll performance
 */
export default angular.module('scrollPointerEventsNone', [])

  .directive('scrollPointerEventsNone', () => {
    return {
      restrict: 'AC',

      link: ($scope, $element) => {
        $element.on('scroll', onScroll);
        $scope.$on('$destroy', () => $element.off('scroll', onScroll));

        let timer;
        function onScroll() {
          clearTimeout(timer);
          $element.addClass('pointer-events-none');
          timer = setTimeout(() => $element.removeClass('pointer-events-none'), 100);
        }
      },
    };
  });
