import './dw-select-user.scss';

/**
 * dwSelectUser directive - wrapper for ffSelect with user selection
 */
export default angular.module('deskworks.dwSelectUser', [
  'deskworks.state',
  'flexForms',
  'deskworks.helper',
])

  .directive('dwSelectUser', function($route, $rootScope, state, helper) {
    return {
      restrict: 'E',
      scope: true,
      compile: function(tElement, tAttrs) {

        const input = angular.element('<ff-static snap-close ng-click="searchUser()" role="button" aria-label="{{\'SIDEBAR.CURRENT_USER\' | translate}}"' +
          ' ng-class="{ ava: state.isUserSelected() && state.getCurrentUser().photo.indexOf(\'missing.png\') < 0 }"></ff-static>');
        const placeholder = angular.element('<span class="placeholder" ng-hide="state.isUserSelected()"' +
          ' translate="SIDEBAR.CURRENT_USER.LABEL"></span>');
        const company = angular.element('<span class="company" ng-show="state.isUserSelected() && state.getCurrentUser().company"' +
          ' ng-bind="state.getCurrentUser().company"></span>');
        const member = angular.element('<span class="member" ng-show="state.isUserSelected()"' +
          ' ng-bind="state.getCurrentUser().fullName"></span>');
        const avatar = angular.element('<ff-img class="avatar" ff-src="{{state.getCurrentUser().photo}}"' +
          ' ng-if="state.isUserSelected()"' +
          ' ff-alt="{{state.getCurrentUser().fullName}}">');
        const dim = angular.element('<div class="ff-dimmer ff-spin" ng-if="spinning()"></div>');

        input.append(placeholder).append(company).append(member).append(avatar).append(dim);
        tElement.append(input).append(avatar);

        return this.link;
      },
      link: function($scope, $element, $attrs, $controller, $transclude) {
        $scope.state = state;
        $scope.helper = helper;

        $scope.searchUser = function searchUser() {
          state.setParam('redirect', $route.current && $route.current.scope && $route.current.scope.backPath ? $route.current.scope.backPath : state.getPath());
          state.setPath('/user-search');
        };

        $scope.spinning = () => state.isUserSelected() && state.getCurrentUser().id !== state.getCurrentUserId() || $scope.loading;

        /**
         * $onDestroy
         */
        $scope.$onDestroy = () => unbindRouteEvents();

        bindRouteEvents();

        /**
         * Bind/unbind route events
         */
        function bindRouteEvents() {
          $scope.offRouteStart = $rootScope.$on('$routeChangeStart', ($event, $next) => {
            if ($next.$$route) $scope.loading = $next.$$route.routeBranch === 'user-search';
          });
          $scope.offRouteSuccess = $rootScope.$on('$routeChangeSuccess', () => { $scope.loading = false; });
          $scope.offRouteError = $rootScope.$on('$routeChangeError', () => { $scope.loading = false; });
        }
        function unbindRouteEvents() {
          $scope.offRouteStart();
          $scope.offRouteSuccess();
          $scope.offRouteError();
        }
      },
    };
  });
