export default angular.module('deskworks.ManualCheckIn', [
  'ngResource',
  'deskworks.config',
])
  .factory('ManualCheckIn', function($resource, $filter, $timeout, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/manual-check-ins/:recurringId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      /**
       * @param params.centerId
       * @param params.userId
       * @returns {*}
       */
      query: params => {
        const { centerId, userId } = params;
        return res.query({ centerId, userId }).$promise
          .then(rbs => (rbs.forEach(rb => { rb.$$count = rb.$$count || rb.checkInCount || 1; }), rbs));
      },

      /**
       * @param params.centerId
       * @param params.userId
       * @param params.recurringId
       * @param params.checkInCount
       * @returns {*}
       */
      save: params => {
        const { centerId, userId, recurringId, checkInCount } = params;
        return res.update({ centerId, userId, recurringId }, { checkInCount }).$promise;
      },
    };
  });
