/**
 * Deskworks alerts service (replacement for $rootScope.showError method)
 */
export default angular.module('deskworks.dwAlerts.service', [])
  .factory('dwAlerts', function($rootScope, $timeout, $q) {

    const dwAlerts = {
      $alerts: [],

      /**
       * Display error message.
       * @param result - rejected promise result (parameter in catch block)
       * @param message - (optional) if missing, result is treated as message
       * @returns {Promise} - rejected promise which can be returned from catch block
       */
      error: function(result, message) {
        if (!message) {
          message = result;
          result = null;
        }

        if (result && result.data && (result.data.error || result.data.message)) {
          message = result.data.error || result.data.message;
        }

        const alert = { id: Date.now(), type: 'danger', message: message || 'Sorry, error occurred. Please try again a bit later.', timeout: 10000 };
        this.showAlert(alert);

        return $q.reject(result || message);
      },

      /**
       * Display info message.
       * @param message - message to display
       */
      info: function(message) {
        const alert = { id: Date.now(), type: 'info', message: message, timeout: 6000 };
        this.showAlert(alert);
      },

      close: function(alert) {
        const index = this.$alerts.indexOf(alert);
        if (index >= 0) {
          this.$alerts.splice(index, 1);
        }
      },

      showAlert: function(alert) {
        const self = this;
        self.$alerts.unshift(alert);
        if (alert.timeout) $timeout(() => { self.close(alert); }, alert.timeout); // hide after timeout
      },
    };

    /**
     * Legacy methods
     */
    $rootScope.showError = function(result, message) {
      return dwAlerts.error(result, message);
    };
    $rootScope.closeError = function(index) {
      dwAlerts.$alerts.splice(index, 1);
    };
    $rootScope.closeAllErrors = function() {
      dwAlerts.$alerts.splice(0, dwAlerts.$alerts.length);
    };

    return dwAlerts;
  });
