import template from './dw-e-check.tpl.html';

/**
 * dwECheck directive - set of inputs standard for eCheck, should be applied as attribute for fieldset tag.
 */
export default angular.module('deskworks.dwECheck', [
  'flexForms',
  'deskworks.helper',
  'deskworks.userProfile.optsBankAccountHolderType',
  'deskworks.userProfile.optsBankAccountType',
])

  .directive('dwECheck', function($compile, $timeout, helper, optsBankAccountHolderType, optsBankAccountType) {
    return {
      template,
      restrict: 'A',
      scope: true,
      controllerAs: 'dwEC',

      controller: function($scope, $element, $attrs) {
        const dwEC = this;

        dwEC.optsBankAccountHolderType = optsBankAccountHolderType;
        dwEC.optsBankAccountType = optsBankAccountType;

        dwEC.check = $scope.$eval($attrs.dwECheck);
      },
    };
  });
