import './dw-alerts.scss';
import template from './dw-alerts.tpl.html';
import uibAlert from './uib-alert';
import dwAlerts from './dw-alerts.service';

/**
 * dwAlerts component - displays alerts from dwAlerts service
 */
export default angular.module('deskworks.dwAlerts', [
  'ngAnimate',
  uibAlert.name,
  dwAlerts.name,
])

  .component('dwAlerts', {
    template,
    controller: 'dwAlertsController',
  })

  .controller('dwAlertsController', function($scope, $timeout, dwAlerts) {
    const $ctrl = this;

    $ctrl.alerts = dwAlerts.$alerts;

    $ctrl.close = alert => { dwAlerts.close(alert); };

    // Start alerts progress after adding
    $scope.$watchCollection('$ctrl.alerts', () => {
      $ctrl.alerts.forEach(alert => {
        if (alert.timeout && !alert.start) {
          $timeout(() => { alert.start = true; }); // start progress bar
        }
      });
    });
  });
