import './dw-modal.scss';

export default angular.module('dwModal', [
  'angularModalService',
])

  .factory('dwModal', function(ModalService) {
    return {
      show: function(options) {
        const defaults = {
          bodyClass: 'dw-modal-open',
          appendElement: angular.element(document.querySelector('.snap-content')),
        };

        return ModalService.showModal(angular.extend({}, defaults, options))
          .then(modal => {
            const cross = modal.element[0].querySelector('.dw-modal-cross');
            if (cross) cross.focus();
            return modal;
          });
      },
    };
  });
