/**
 * Isolated ui.bootstrap.alert module from ui.bootstrap package
 */
export default angular.module('ui.bootstrap.alert', [])

  .controller('UibAlertController', ['$scope', '$attrs', '$interpolate', '$timeout', function($scope, $attrs, $interpolate, $timeout) {
    $scope.closeable = !!$attrs.close;

    const dismissOnTimeout = angular.isDefined($attrs.dismissOnTimeout) ?
      $interpolate($attrs.dismissOnTimeout)($scope.$parent) : null;

    if (dismissOnTimeout) {
      $timeout(() => { $scope.close(); }, parseInt(dismissOnTimeout, 10));
    }
  }])

  .directive('uibAlert', function() {
    return {
      controller: 'UibAlertController',
      controllerAs: 'alert',
      template:
        '<div class="alert" ng-class="[\'alert-\' + (type || \'warning\'), closeable ? \'alert-dismissible\' : null]" role="alert">' +
          '<button ng-show="closeable" type="button" class="close" ng-click="close({$event: $event})">' +
            '<span aria-hidden="true">&times;</span>' +
          '</button>' +
          '<div ng-transclude></div>' +
        '</div>',
      transclude: true,
      replace: true,
      scope: {
        type: '@',
        close: '&',
      },
    };
  });
