/**
 * Options for Membership Filter
 */
export default angular.module('deskworks.optsMembershipFilter', [])

  .factory('optsMembershipFilter', function($rootScope, $translate) {
    const opts = [
      { id: 'all', _name: 'USER_SEARCH.FILTERS.ALL', value: false, type: 'all' },
      { id: 'activeMembers', _name: 'USER_SEARCH.FILTERS.ACTIVE_MEMBERS', value: true, type: 'role' },
      { id: 'inactiveMembers', _name: 'USER_SEARCH.FILTERS.INACTIVE_MEMBERS', value: false, type: 'role' },
      { id: 'activeCoMembers', _name: 'USER_SEARCH.FILTERS.ACTIVE_CO_MEMBERS', value: false, type: 'role' },
      { id: 'inactiveCoMembers', _name: 'USER_SEARCH.FILTERS.INACTIVE_CO_MEMBERS', value: false, type: 'role' },
      { id: 'nonMembers', _name: 'USER_SEARCH.FILTERS.NON_MEMBERS', value: false, type: 'role' },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(opt => { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
