import template from './dw-credit-card.tpl.html';

/**
 * dwCreditCard directive - set of inputs standard for CC, should be applied as attribute for fieldset tag.
 */
export default angular.module('deskworks.dwCreditCard', [
  'flexForms',
  'deskworks.helper',
  'deskworks.userProfile.optsCCExpiryMonth',
  'deskworks.userProfile.optsCCExpiryYear'
])

  .directive('dwCreditCard', function() {

    return {
      template,
      restrict: 'A',
      scope: true,
      controller: 'dwCreditCardCtrl as $ctrl',
      bindToController: {
        cc: '<dwCreditCard',
        ccTypes: '<dwCreditCardTypes',
      },
    };
  })

  .controller('dwCreditCardCtrl', function($scope, optsCCExpiryMonth, optsCCExpiryYear) {
    const $ctrl = this;

    $ctrl.optsCCExpiryMonth = optsCCExpiryMonth;
    $ctrl.optsCCExpiryYear = optsCCExpiryYear;

    $scope.$watch('$ctrl.ccType', () => { $ctrl.cc.type = $ctrl.ccType || $ctrl.cc.type; });
  });
