export default angular.module('deskworks.MessageBoardUserSettings', [
  'ngResource',
  'deskworks.config',
])

  .factory('MessageBoardUserSettings', function($resource, $q, deskworksConfig, helper) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/dw-mail/message-board/users/:userId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      get: function(userId) {
        if (!userId || helper.isUid(userId)) return $q.when({ notification: 'immediately' });
        return res.get({ userId }).$promise;
      },

      save: (userId, data) => res.update({ userId }, data).$promise,
    };
  });
