export default angular.module('deskworks.UserField', [
  'ngResource',
  'deskworks.config',
])

  .factory('UserField', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/user-fields/:fieldId'), {}, {
      update: { method: 'PUT' }
    });

    return {
      query: () => res.query().$promise,
      save: field => {
        if (field.id) return res.update({ fieldId: field.id }, field).$promise;
        return res.save(field).$promise
          .then(result => { field.id = result.id; });
      },
      delete: fieldId => res.delete({ fieldId }).$promise,
    };
  });
