export default angular.module('dwPhoneDirective', [])

  .directive('dwPhone', function($compile, $translate) {
    return {
      restrict: 'AC',
      priority: 30,
      terminal: true,

      compile: function(tElement, tAttrs) {
        const self = this;
        tAttrs.$set('maxlength', tAttrs.maxlength || 64);
        // tAttrs.$set('ffPattern', tAttrs.ffPattern || /^((\+\d{1,3}[-. ]?\(?\d\)?[-. ]?\d{1,5})|(\(?\d{2,6}\)?))[-. ]?(\d{3,4})[-. ]?(\d{3,4})?$/);
        tAttrs.$set('ffPattern', tAttrs.ffPattern || /^(?:\+\d[-. ]?)?(?:\(?\d\)?[-. ]?){5,13}\d$/);
        tAttrs.$set('placeholder', tAttrs.placeholder || '888-379-2865');

        const number = $translate.instant('USER_PROFILE.PHONES.NUMBER');
        const patternMsg = $translate.instant('GLOBAL.VALIDATION.PHONE');
        tAttrs.$set('ffValidateAlert', tAttrs.ffValidateAlert || "{ field: '" + number +  "', pattern: '" + patternMsg +  "' }");

        return function link($scope, $element, $attrs) {
          $compile($element, null, self.priority)($scope);
        };
      }
    };
  });
