import fdFieldTemplates from './fd-field-templates.service';

/**
 * fdField directive - expands to template appropriate for field type
 */
export default angular.module('deskworks.formDesign.fdField', [fdFieldTemplates.name])

  .directive('fdField', function($compile, fdFieldTemplates) {
    return {
      scope: true,
      require: "^^?form",
      link: function($scope, $element, $attrs, form) {
        $scope.field = $scope.$eval($attrs.field);
        const el = angular.element(fdFieldTemplates[$scope.field.type]);
        $element.append(el);
        $compile(el)($scope);
      },
    };
  });
