export default {

  routes: [
    { url: 'reservation', id: 'reservation' },
    { url: 'sign-in', id: 'signIn' },
    { url: 'confirm', id: 'confirm' },
    { url: 'complete', id: 'complete' },
    { url: 'purchase', id: 'purchase' },
    { url: 'sign-up', id: 'signUp' },
    { url: 'staff-help', id: 'staffHelp' },
  ],

  /**
   * 02. Reservation
   */
  reservation: {
    template: '<res-reservation center="$resolve.center" cal-data="$resolve.calData"' +
      ' hide-reservation-cost-estimate="$resolve.settings.hideReservationCostEstimate"' +
      ' hide-recurring-reservations="$resolve.settings.hideRecurringReservations"' +
      ' hide-staff-only-units="$resolve.settings.hideStaffOnlyUnits"' +
      ' reservation-fields="$resolve.reservationFields" hide-calendar-btn="true"' +
      ' back="$ctrl.back()" next="$ctrl.next()"></res-reservation>',
    controller: 'WizardReservationReservationCtrl as $ctrl',
    hideBanner: true,
    class: 'wizard-reservation-reservation',
    userTasksPolicy: false,
    reloadOnSearch: false,
    translations: ['reservations', 'book-it-now'],
    resolve: {
      /*@ngInject*/ calData: ($filter, state, helper, dwAlerts, calendarService, resolveTimezone) => resolveTimezone()
        .then(() => {
          let date = $filter('toDate')(state.getParam('date'), 'Z');
          date = moment.isMoment(date) ? date : moment();
          return calendarService.query(state.getCurrentCenterId(), date, date, state.getParam('role'))
            .catch(err => dwAlerts.error(err, 'Failed to fetch calendar data.'));
        }),

      /*@ngInject*/ center: (state, dwAlerts, centerService) => centerService.get(state.getCurrentCenterId())
        .catch(err => dwAlerts.error(err, 'Failed to get center.')),

      /*@ngInject*/ settings: (dwAlerts, setupService) => setupService.getSetupSettings()
        .catch(err => dwAlerts.error(err, 'Failed to get setup settings.')),

      /*@ngInject*/ reservationFields: (dwAlerts, ReservationField) => ReservationField.query()
        .catch(err => dwAlerts.error(err, 'Failed to query reservation fields.')),
    },
  },

  /**
   * 03. Sign In
   */
  signIn: {
    template: '<dw-sign-in id="signIn" show-sign-up="true"></dw-sign-in>',
    controller: 'WizardReservationSignInCtrl as $ctrl',
    userTasksPolicy: false,
    translations: ['sign-in'],
    resolve: {},
  },

  /**
   * 04. Confirm (for member/admin or on free reservation)
   */
  confirm: {
    template: '<div ng-include="\'wizard-reservation/04-confirm/confirm.tpl.html\'"></div>',
    controller: 'WizardReservationConfirmCtrl as $ctrl',
    userTasksPolicy: false,
    reloadOnSearch: false,
    translations: ['reservations'],
    resolve: {
      /*@ngInject*/ center: (state, dwAlerts, centerService, routeHelper) => routeHelper.auth(`/${state.getPath().split('/')[1]}/sign-in`)
        .then(() => centerService.get(state.getCurrentCenterId())
          .catch(err => dwAlerts.error(err, 'Failed to get center.'))),

      /*@ngInject*/ settings: (dwAlerts, setupService) => setupService.getSetupSettings()
        .catch(err => dwAlerts.error(err, 'Failed to get setup settings.')),

      /*@ngInject*/ reservationUnit: ($q, $filter, state, helper, routeHelper, dwAlerts, calendarService, resolveTimezone) =>
        $q.all([routeHelper.auth(`/${state.getPath().split('/')[1]}/sign-in`), resolveTimezone()])
          .then(() => {
            let date = $filter('toDate')(state.getParam('date'), 'Z');
            date = moment.isMoment(date) ? date : moment();
            return calendarService.query(state.getCurrentCenterId(), date, date)
              .catch(err => dwAlerts.error(err, 'Failed to fetch calendar data.'))
              .then(types => types.reduce((result, type) => result || type.reservationUnits.find(unit => unit.id === +state.getParam('unit')), null));
          }),

      /*@ngInject*/ reservationFields: (dwAlerts, ReservationField, session) => session.wait().catch(angular.noop)
        .then(() => ReservationField.query()
          .catch(err => dwAlerts.error(err, 'Failed to query reservation fields.'))),
    },
  },

  /**
   * 05. Complete
   */
  complete: {
    template: '<div ng-include="\'wizard-reservation/05-complete/complete.tpl.html\'"></div>',
    controller: 'WizardReservationCompleteCtrl as $ctrl',
    userTasksPolicy: false,
    translations: ['reservations', 'simple-reservation', 'book-it-now'],
    resolve: {
      /*@ngInject*/ settings: (dwAlerts, setupService) => setupService.getSetupSettings()
        .catch(err => dwAlerts.error(err, 'Failed to get setup settings.')),
    },
  },

  /**
   * 06. Purchase
   */
  purchase: {
    template: '<div ng-include="\'wizard-reservation/06-purchase/purchase.tpl.html\'"></div>',
    controller: 'WizardReservationPurchaseCtrl as $ctrl',
    userTasksPolicy: false,
    reloadOnSearch: false,
    translations: ['simple-reservation', 'reservations', 'purchase', 'billing-profile', 'book-it-now'],
    resolve: {
      /*@ngInject*/ center: (state, dwAlerts, centerService) => centerService.get(state.getCurrentCenterId())
        .catch(err => dwAlerts.error(err, 'Failed to get center.')),

      /*@ngInject*/ purchase: ($q, dwAlerts, state, routeHelper, purchaseService, resolveTimezone) =>
        $q.all([routeHelper.auth(`/${state.getPath().split('/')[1]}/sign-in`), resolveTimezone()])
          .then(() => purchaseService.get(state.getCurrentCenterId(), state.getCurrentUserId(), +state.getParam('purchaseId'))
            .catch(err => dwAlerts.error(err, 'Failed to get purchase.'))),

      /*@ngInject*/ reservationUnit: ($q, $filter, state, helper, routeHelper, dwAlerts, calendarService, resolveTimezone) =>
        $q.all([routeHelper.auth(`/${state.getPath().split('/')[1]}/sign-in`), resolveTimezone()])
          .then(() => {
            let date = $filter('toDate')(state.getParam('date'), 'Z');
            date = moment.isMoment(date) ? date : moment();
            return calendarService.query(state.getCurrentCenterId(), date, date)
              .catch(err => dwAlerts.error(err, 'Failed to fetch calendar data.'))
              .then(types => types.reduce((result, type) => result || type.reservationUnits.find(unit => unit.id === +state.getParam('unit')), null));
          }),

      /*@ngInject*/ user: ($q, dwAlerts, state, routeHelper, userProfileService, resolveTimezone) =>
        $q.all([routeHelper.auth(`/${state.getPath().split('/')[1]}/sign-in`), resolveTimezone()])
          .then(() => userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
            .catch(err => dwAlerts.error(err, 'Failed to get user profile.'))),

      /*@ngInject*/ settings: (dwAlerts, setupService) => setupService.getSetupSettings()
        .catch(err => dwAlerts.error(err, 'Failed to get setup settings.')),

      /*@ngInject*/ reservationFields: (dwAlerts, ReservationField, session) => session.wait().catch(angular.noop)
        .then(() => ReservationField.query()
          .catch(err => dwAlerts.error(err, 'Failed to query reservation fields.'))),
    },
  },

  /**
   * 07. Sign Up
   */
  signUp: {
    template: '<div ng-include="\'wizard-reservation/07-sign-up/sign-up.tpl.html\'"></div>',
    controller: 'WizardReservationSignUpCtrl as $ctrl',
    userTasksPolicy: false,
    translations: ['user-profile'],
    resolve: {
      /*@ngInject*/ reg: ($route, state) =>({ member: false, selfRegistration: true, new: !state.getCurrentUserId()}),

      /*@ngInject*/ user: (dwAlerts, $route, helper, state, session, userProfileService, centerService) => {
        if (!state.getCurrentUserId()) {
          return centerService.get(state.getCurrentCenterId())
            .then(center => userProfileService.new(center, 'nonMember', 'signUp')
              .catch(err => dwAlerts.error(err, 'Failed to get new member profile template.')));
        } else {
          return session.wait().catch(angular.noop)
            .then(() => userProfileService.getForRegistration(state.getCurrentCenterId(), state.getCurrentUserId(), 'nonMember', 'signUp')
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        }
      },

      /*@ngInject*/ center: (state, dwAlerts, centerService) => centerService.get(state.getCurrentCenterId())
        .catch(err => dwAlerts.error(err, 'Failed to get center.')),
    }
  },

  /**
   * xx. Staff Help
   */
  staffHelp: {
    template: '<staff-help></staff-help>',
    userTasksPolicy: false,
    translations: ['simple-reservation'],
    resolve: {
      /*@ngInject*/ auth: (routeHelper, state) => routeHelper.auth(`/${state.getPath().split('/')[1]}/sign-in`),
    },
  },
};
