import './message-board-user-settings.service';
import './opts-message-board-notices.service';
import template from './message-board-user-settings.tpl.html';

/**
 * messageBoardUserSettings directive - Message Board -> User Settings directive
 */
export default angular.module('deskworks.messageBoardUserSettings', [
  'deskworks.MessageBoardUserSettings',
  'deskworks.optsMessageBoardNotices',
])

  .directive('messageBoardUserSettings', function() {
    return {
      template,
      restrict: 'EAC',
      scope: true,
      controller: 'MessageBoardUserSettingsCtrl as $ctrl',
      bindToController: {
        userId: '<',
        save: '=',
      },
    };
  })

  .controller('MessageBoardUserSettingsCtrl', function($scope, $timeout, state, helper, dwAlerts,
                                                       MessageBoardUserSettings, optsMessageBoardNotices) {
    const $ctrl = this;

    $ctrl.optsMessageBoardNotices = optsMessageBoardNotices;

    /**
     * $onInit
     */
    $ctrl.$onInit = function() {
      $ctrl.save = save;
      MessageBoardUserSettings.get($ctrl.userId)
        .catch(err => dwAlerts.error(err, 'Failed to get message board user settings.'))
        .then(settings => { $ctrl.settings = settings; });
    };

    /**
     * Save user settings
     */
    function save() {
      return $timeout(() => MessageBoardUserSettings.save($ctrl.userId, $ctrl.settings)
        .catch(err => dwAlerts.error(err, 'Failed to save message board user settings.')));
    }
  });
