import './membership-filter.scss'
import template from './membership-filter.tpl.html';
import optsMembershipFilter from 'shared/membership-filter/opts-membership-filter.service';

/**
 * membershipFilter directive - component for selecting membership in user search and membership report
 */
export default angular.module('deskworks.membershipFilter', [
  'flexForms',
  'deskworks.state',
  'deskworks.helper',
  optsMembershipFilter.name,
])

  .directive('membershipFilter', function() {
    return {
      template,
      restrict: 'E',
      scope: true,
      controller: 'MembershipFilterCtrl as $ctrl',
      bindToController: {
        ffModel: '=?',
        filterData: '=?',
        layout: '@?',
      },
    };
  })

  .controller('MembershipFilterCtrl', function($scope, $element, $attrs, state, helper, optsMembershipFilter) {
    const $ctrl = this;

    $ctrl.$onInit = function() {
      $ctrl.filterData = $ctrl.filterData || angular.copy(optsMembershipFilter);
      updateModel();
    };

    /**
     * Filter checkbox change handler
     */
    $ctrl.onChange = function(filter) {
      if (filter.id === 'all') {
        const val = helper.find($ctrl.filterData, 'all').value;
        $ctrl.filterData.forEach(function(filter) {
          if (filter.id === 'all') return;
          filter.value = val;
        });
        if (!val) {
          helper.find($ctrl.filterData, 'activeMembers').value = true;
        }
      } else {
        helper.find($ctrl.filterData, 'all').value = $ctrl.filterData
          .filter(filter => filter.id !== 'all')
          .every(filter => filter.value);
      }
      updateModel();
    };

    function updateModel() {
      $ctrl.ffModel = $ctrl.filterData
        .filter(filter => filter.id !== 'all')
        .reduce((obj, filter) => { if (filter.value) obj[filter.id] = filter.value; return obj; }, {});
    }
  });
