import template from './dw-invoice.tpl.html';

/**
 * dwInvoice directive - component for displaying Deskworks specific invoices
 */
export default angular.module('deskworks.dwInvoice', [
  'flexForms',
  'deskworks.state',
  'deskworks.helper',
])

  .directive('dwInvoice', function() {
    return {
      template,
      restrict: 'E',
      scope: true,
      controller: 'dwInvoiceCtrl as dwInvoice',
      bindToController: {
        center: '=*?',
        invoice: '=*',
        allowRemove: '@',
        updateInvoice: '&',
      },

      compile: function(tElement, tAttrs) {
        tElement.addClass('dw-invoice');
        return function link() {};
      }
    };
  })

  .controller('dwInvoiceCtrl', function($scope, $element, $attrs, dwAlerts, state, purchaseService) {
    const dwInvoice = this;

    /**
     * Remove/Void Item handler
     */
    dwInvoice.removeItem = function(item) {
      return purchaseService.deletePurchaseItem(state.getCurrentCenterId(), state.getCurrentUserId(), dwInvoice.invoice.id, item.id)
        .catch(err => dwAlerts.error(err, 'Failed to remove purchase item.'))
        .then(result => dwInvoice.updateInvoice());
    };
  });

