/**
 * niceTime directive - expands to hh:mm<sup>a</sup> or HH:mm depending on $locale.timeFormat
 */
export default angular.module('niceTimeDirective', [])

  .directive('niceTime', function($compile, $locale) {
    return {
      restrict: 'A',
      compile: function(tElement, tAttrs) {
        return function($scope, $element, $attrs) {
          let tpl;
          if ($locale.timeFormat === 24) {
            const format = tAttrs.niceFormat === 'shortHours' ? 'H' : 'HH:mm';
            tpl = `<span ng-bind="${tAttrs.niceTime} | date: '${format}'"></span>`;
          } else {
            const format = tAttrs.niceFormat === 'shortHours' ? 'h' : 'hh:mm';
            tpl = `<span ng-bind="${tAttrs.niceTime} | date: '${format}'"></span><sup ng-bind="${tAttrs.niceTime} | date: 'a'"></sup>`;          }
          $element.empty().append($compile(tpl)($scope));
        };
      }
    };
  });
