export default angular.module('deskworks.ReservationUnit', [
  'ngResource',
  'ngFileUpload',
  'deskworks.config',
])

  .factory('ReservationUnit', ($resource, $q, Upload, deskworksConfig) => {

    const res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/reservation-units/:id'), {}, {
      update: { method: 'PUT' },
    });

    return {
      /**
       * Query
       */
      query: (centerId) => res.query({ centerId }).$promise,

      /**
       * Get
       */
      get: (centerId, id) => id === 'new' ? $q.when({ id: 'new', expectedUsePerMonth: 240 }) : res.get({ centerId, id }).$promise,

      /**
       * Save
       */
      save: (centerId, resUnit) => resUnit.id && resUnit.id !== 'new' ?
        res.update({ centerId, id: resUnit.id }, resUnit).$promise :
        res.save({ centerId }, resUnit).$promise
          .then(result => (resUnit.id = result.id, result)),

      /**
       * Delete
       */
      delete: (centerId, id) => res.delete({ centerId, id }).$promise,

      /**
       * Upload photo
       */
      uploadPhoto: (centerId, id, file) => Upload.upload({
        url: deskworksConfig.getApiAbsUrl(`/centers/${centerId}/reservation-units/${id}`),
        method: 'PUT',
        withCredentials: true,
        data: { photo: file },
      }),

      /**
       * Delete photo
       * @param params.centerId
       * @param params.id
       * @returns {Resource.$promise}
       */
      deletePhoto: params => res.update(params, { photo: null }).$promise,
    };
  });
