import template from './forbid.tpl.html';

/**
 * forbid directive - forbids staff/members to access content.
 * E.g. prevent repeating self-registration in same center.
 */
export default angular.module('forbid', [])

  .directive('forbid', function($compile, session, state, userProfileService) {
    return {
      restrict: 'EA',
      scope: true,
      controller: 'ForbidCtrl as $forbid',
      bindToController: {
        admin: '<?',
        member: '<?',
      },

      link: ($scope, $element, $attrs, $forbid) => {
        const content = $element.children().css({ display: 'none' });
        session.wait()
          .then(() => {
            if ($forbid.member) {
              $scope.$watch(() => state.getCurrentCenterId(), centerId => {
                $forbid.center = $forbid.member.activatedAt.find(i => i.centerId === centerId);
                if ($forbid.center) {
                  content.css({ display: 'none' });
                  $element.append($compile(template)($scope));
                  $forbid.forbid = 'member';
                } else {
                  content.css({ display: null });
                  const el = $element[0].querySelector('#forbid');
                  if (el) { el.remove(); }
                  $forbid.forbid = null;
                }
              });
            } else if ($forbid.admin && session.isAdmin()) {
              $element.append($compile(template)($scope));
              $forbid.forbid = 'admin';
            } else {
              content.css({ display: null });
            }
          })
      },
    };
  })

  .controller('ForbidCtrl', function($scope, $route, $timeout, state) {
    const $forbid = this;

    $forbid.signOut = () => state.logout({ skipResetParams: true })
        .finally(() => ($route.reload(), $timeout(5000)));

    $forbid.home = () => {
      state.resetParams();
      return state.setPath('/');
    };
  });
