/**
 * dwSelectProfile directive - sidebar widget for co-members to switch to one of master member profiles
 */
export default angular.module('deskworks.dwSelectProfile', [
  'ngRoute',
  'deskworks.state',
  'flexForms',
  'deskworks.helper',
  'deskworks.userProfile.service'
])

  .directive('dwSelectProfile', function($route, state, helper, userProfileService, resolveTimezone) {
    return {
      restrict: 'AC',
      scope: true,

      template:
        '<span>Current Profile</span>' +
        '<ff-select name="currentProfile" ff-model="currentProfile" ff-options="profile.name for profile in profiles" ff-change="selectProfile(currentProfile)"></ff-select>',

      compile: function(tElement, tAttrs) {
        tElement.css('display', 'none');

        return function link($scope, $element, $attrs) {
          $scope.profiles = [];

          /**
           * Logged User ID watcher, if logged, gets user profile from API and populates profiles collection.
           */
          $scope.$watch(function() { return state.getLoggedUserId(); }, function(val) {
            if (state.isLoggedAdmin()) {
              $element.css('display', 'none');
              return;
            }

            if(val) {
              resolveTimezone()
                .then(() => userProfileService.get(state.getCurrentCenterId(), state.getLoggedUserId()))
                .then(result => {
                  // Add logged user to available profiles
                  $scope.profiles = [{ id: result.id, name: result.firstName + ' ' + result.lastName }];

                  // Add master members to available profiles
                  result.masterMembers.forEach(masterMember => {
                    if (masterMember.id !== val) {
                      $scope.profiles.push({ id: masterMember.id, name: masterMember.company || masterMember.fullName });
                      state.setCurrentUserId(masterMember.id);
                    }
                  });

                  $scope.currentProfile = helper.find($scope.profiles, state.getCurrentUserId()) || $scope.profiles[0];

                  if ($scope.profiles.length > 1) $element.css('display', '');
                });
            } else {
              $scope.profiles = [];
              $scope.currentProfile = null;
              $element.css('display', 'none');
            }
          });

          /**
           * Selected User ID watcher, switches current profile in select
           */
          $scope.$watch(() => state.getCurrentUserId(), val => {
            $scope.currentProfile = helper.find($scope.profiles, val) || $scope.profiles[0];
          });

          /**
           * ngChange handler for profiles select
           */
          $scope.selectProfile = function(profile) {
            state.setCurrentUserId(profile.id === state.getLoggedUserId() ? null : profile.id);

            // Force reloading current route if it doesn't reload on search
            if ($route.current && $route.current.$$route && !$route.current.$$route.reloadOnSearch) $route.reload();
          };
        };
      }
    };
  });
