export default angular.module('dwSearchUserDirective', [])

  .directive('dwSearchUser', function($compile, $q, $filter, ffHelper, state, userProfileService, dwAlerts) {
    return {
      restrict: 'A',
      priority: 30,
      terminal: true,
      template:
      '<ff-search-options>' +
        '<div>' +
          '<h6>' +
            '<span ng-bind-html="$search.highlighted.fullName"></span>{{$search.highlighted.company ? \',\' : \'\'}} ' +
            '<span ng-bind-html="$search.highlighted.company"></span> ' +
            '&lt;<span ng-bind-html="$search.highlighted.email"></span>&gt;' +
          '</h6>' +
          '<small>' +
            '<span ng-repeat="match in $search.matches" class="match">{{match.name}}: <span ng-bind-html="(match.value || \'-\') + ($last ? \'\' : \', \')"></span></span>' +
          '</small>' +
        '</div>' +
      '</ff-search-options>' +
      '<ff-search-match ng-if="dwSearchUser.options !== \'activeNameOnly\'">' +
        '<h6>' +
          '<span ng-bind="ffSearch.ngModel.$modelValue.fullName"></span>{{ffSearch.ngModel.$modelValue.company ? \',\' : \'\'}} ' +
          '<span ng-bind="ffSearch.ngModel.$modelValue.company"></span> ' +
          '&lt;<span ng-bind="ffSearch.ngModel.$modelValue.email"></span>&gt;' +
        '</h6>' +
      '</ff-search-match>' +
      '<ff-search-match ng-if="dwSearchUser.options === \'activeNameOnly\'">' +
        '<h6>' +
          '<span ng-bind="ffSearch.ngModel.$modelValue.fullName"></span>' +
        '</h6>' +
      '</ff-search-match>',

      controllerAs: 'dwSearchUser',
      bindToController: {
        options: '@dwSearchUser'
      },

      controller: function() {
        const dwSearchUser = this;

        /**
         * Filters users by found search string and sorts by number of matches
         * @param $results - non-filtered search results
         * @param $search - search string
         * @returns {Array.<T>} - filtered results
         */
        dwSearchUser.filterUsers = function($results, $search) {
          $results = $results || [];

          $results.forEach(user => {
            user.weight = 0;
            user.matches = [{ name: 'login', value: $filter('highlight')(user.login, $search) }];

            user.highlighted = {
              fullName: $filter('highlight')(user.fullName, $search),
              company: $filter('highlight')(user.company, $search),
              email: $filter('highlight')(user.email, $search)
            };

            for (let prop in user) {
              if (user.hasOwnProperty(prop) && angular.isString(user[prop]) && prop.indexOf('$$') !== 0) {
                const index = user[prop].toLowerCase().indexOf($search.toLowerCase());
                if (index >= 0) {
                  user.weight++;
                  if (prop !== 'fullName' && prop !== 'company' && prop !== 'email' && prop !== 'login') {
                    const name = prop.split(/(?=[A-Z])/).join(' ').toLowerCase();
                    user.matches.push({ name: name, value: $filter('highlight')(user[prop], $search) });
                  }
                }
              }
            }
          });

          return $results
            .filter(user => user.weight > 0)
            .sort((a, b) => a.weight < b.weight ? 1 : (a.weight > b.weight ? -1 : 0));
        };

        /**
         * Search user handler, calls API search and returns resulting promise
         * @param $search - search string
         */
        dwSearchUser.searchUser = function($search) {
          return userProfileService.quickSearch(state.getCurrentCenterId(), $search)
            .catch(err => dwAlerts.error(err, 'Failed to quick search users.'));
        };
      },

      compile: function(tElement, tAttrs) {
        const self = this;

        tAttrs.$set('ffSearch', 'dwSearchUser.searchUser($search)');
        tAttrs.$set('ffSearchFilter', 'dwSearchUser.filterUsers($results, $search)');
        tAttrs.$set('placeholder', 'start typing to search users');

        return function link($scope, $element, $attrs) {
          $compile($element, null, self.priority)($scope);
        };
      },
    };
  });
