export default angular.module('dwLastNameDirective', [])

  .directive('dwLastName', function($compile, $translate) {
    return {
      restrict: 'AC',
      priority: 30,
      terminal: true,

      compile: function(tElement, tAttrs) {
        const self = this;

        tAttrs.$set('maxlength', tAttrs.maxlength || 256);
        tAttrs.$set('ffPattern', tAttrs.ffPattern || /^[^\\<>\/&]*$/);
        tAttrs.$set('placeholder', tAttrs.placeholder || 'Doe');

        const lastName = $translate.instant('USER_PROFILE.PERSONAL_INFO.LAST_NAME');
        const patternMsg = $translate.instant('USER_PROFILE.PERSONAL_INFO.LAST_NAME_INVALID_CHARS');
        tAttrs.$set('ffValidateAlert', tAttrs.ffValidateAlert || "{ field: '" + lastName +  "', pattern: '" + patternMsg +  "' }");

        return function link($scope, $element, $attrs) {
          $compile($element, null, self.priority)($scope);
        };
      }
    };
  });
